import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { SupplierLiabilityComponent } from './supplier-liability.component';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';

const routes: Routes = [
  {
    path: '',
    component: SupplierLiabilityComponent
  },
  {
    path: 'detail-supplier-liability',
    loadChildren: () => import('./detail-supplier-liability/detail-supplier-liability.module').then(m => m.DetailSupplierLiabilityModule)
  }
]

@NgModule({
  declarations: [SupplierLiabilityComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    FormsModule,
    ReactiveFormsModule,
    TranslateModule,
    IonicModule,
  ]
})
export class SupplierLiabilityModule { }
